.dropdown-content,
.dropdown .sub-menu {
  display: none;
  position: absolute;
  top: 100%;
}
.dropdown-content.left,
.dropdown .sub-menu.left {
  left: 0;
}
.dropdown-content.right,
.dropdown .sub-menu.right {
  right: 0;
}
.dropdown-container {
  position: relative;
}
.dropdown {
  position: relative;
}
